@import 'src/styles/Mixins';
@import 'src/styles/Variables';

.reviewsBlock {
  width: 100%;
  background-color: white;
  margin-bottom: 60px;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0 0 16px 0;
  border-radius: 5px;


  .reviewsBlockTitle {
    @include SFDisplaySemibold();
    font-size: 28px;
    line-height: 32px;
    text-align: center;
    padding-top: 24px;
    margin-bottom: 24px;
    color: $textColor;
  }

  & .reviewsContainer {
    width: 100%;

    .window {
      width: 100%;
      overflow: hidden;

      & .allReviewsItemsContainer {
        @include flex(row, space-between, stretch);
        width: 100%;
        transition: translate;
        transition-property: transform;
        transition-duration: 500ms;
        transition-timing-function: ease-in-out;

        & .reviewContainer {
          min-width: 100%;
          max-width: 100%;
          padding: 0 10px;

          .reviewsBlockSubTitle {
            @include SFDisplaySemibold();
            @include fontSize16();
            text-align: center;
            margin-bottom: 12px;
          }

          .reviewsBlockText {
            @include SFTextRegular();
            font-size: 14px;
            line-height: 140%;
            text-align: center;
            max-width: 718px;
            margin: 0 auto 12px;
            color: $textColor;
          }

          .reviewsBlockWrapper {
            display: flex;
            justify-content: center;

            .reviewsBlockPhoneWrapper {
              display: flex;
              align-items: center;
              margin-right: 16px;

              & p {
                @include SFTextRegular();
                font-size: 16px;
                line-height: 24px;
                color: $opacityColor;
              }

              & img {
                @include size(11.08px, 15.83px);
                margin-right: 6.55px;
              }
            }

            & p {
              @include SFTextRegular();
              font-size: 16px;
              line-height: 24px;
              color: $opacityColor;
            }
          }
        }
      }
    }
  }

  .buttonsBlock {
    display: flex;
    flex-direction: row;
    align-items: center;

    & p {
      margin: 0 32px 0 48px;
      @include SFTextRegular();
      font-size: 16px;
      line-height: 24px;
      color: #916A00;
    }
  }
}

@media (max-width: 768px) {
  .reviewsBlock {
    margin-bottom: 32px;

    .reviewsBlockTitle {
      @include SFDisplayBold();
      font-size: 21px;
      line-height: 28px;
      padding-top: 16px;
      margin-bottom: 12px;
    }

    .reviewsContainer {

      .window {

        .allReviewsItemsContainer {

          .reviewContainer {

            padding-bottom: 0;

            .reviewsBlockSubTitle {
              font-size: 15px;
              line-height: 20px;
            }

            .reviewsBlockText {
              font-size: 13px;
              line-height: 20px;
            }

            .reviewsBlockWrapper {
              flex-direction: column;
              align-items: center;

              .reviewsBlockPhoneWrapper {
                margin-right: 0 !important;
                margin-bottom: 8px;

                & p {
                  font-size: 15px !important;
                  line-height: 20px !important;
                }
              }

              & p {
                font-size: 15px !important;
                line-height: 20px !important;
              }
            }
          }
        }
      }
    }
  }
}
